.Title {
  margin-right: 2rem;
  font-size: 1.5em;
  font-family: var(--serif);
  font-style: italic;
  font-weight: 200;
  text-shadow: 0.1rem 0.1rem 0.25rem rgba(255, 192, 203, 0.5);
}

.About .Quote {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: column wrap;
      flex-flow: column wrap;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-line-pack: center;
      align-content: center;
  font-family: var(--serif);
  font-style: italic;
  font-weight: 200;
  color: var(--light);
  line-height: 1.3rem;
  font-size: 1rem;
}
.About .Quote p {
  display: inline-block;
  margin: 0;
  padding: 0;
}

.About .Quote p:before {
  content: '"';
  color: var(--pink);
  font-size: calc(1rem + 1vw);
  position: relative;
  left: -0.5rem;
}
.About .Quote p:after {
  content: '"';
  color: var(--pink);
  font-size: calc(1rem + 1vw);
  position: absolute;
}

.About .Quote span {
  color: var(--pink);
  font-family: var(--sans);
  font-weight: 400;
  font-style: normal;
  -ms-flex-item-align: end;
      align-self: flex-end;
  position: relative;
  bottom: 1rem;
}
@media (max-width: 500px) {
  .App-header {
    height: 10vh;
  }
  .Title {
    font-size: 1.2rem;
  }
}

.About {
  position: fixed;
  top: 100px;
  left: 0px;
  right: 0px;
  bottom: 0px;
  background-color: var(--med);
  color: #ddd;
  overflow: hidden;
  display: -ms-flexbox;
  display: flex;
  overflow-x: hidden;
  overflow-y: scroll;
  -ms-flex-flow: row wrap;
      flex-flow: row wrap;
}

.About article {
  margin: 0 2em;
  padding: 24px 0 0;
  min-width: 40%;
  max-width: 500px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  position: relative;
}

.About article h4 {
  color: #ccc;
  font-family: var(--sans);
  font-weight: 300;
  font-size: 10px;
  line-height: 1em;
  letter-spacing: normal;
  display: none;
}

.About article h1 {
  /* position: absolute; */
  display: block;
  font-weight: 300;
  padding: 80px 0;
  font-size: 36px;
  font-size: calc(48px + 3vw);
  letter-spacing: -2px;
}

.About article p {
  font-family: var(--sans);
  font-weight: 300;
  margin-bottom: 28px;
}

.About article a {
  text-decoration: none;
  color: #fff;
  border-bottom: 1px solid var(--light);
}

.About article a:hover {
  border-bottom: 1px solid var(--pink);
}

.About article#recognition #recognition-lists {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: row wrap;
      flex-flow: row wrap;
  position: absolute;
  top: 30%;
}

.About article#recognition ul {
  min-width: 40%;
  margin-bottom: 28px;
}

.About article#recognition ul li {
  color: #888;
  font-size: 14px;
  line-height: 28px;
  list-style: none;
}

.About article#recognition ul li:first-child {
  color: #ddd;
}

.Bio {
  padding: 1rem;
}

@media all and (min-width: 768px) {
  .About article {
    margin: 0 4%;
  }
  .About article h4 {
    font-size: 12px;
  }
}



